body { padding-top: 60px; }

.container {
    width: 100%;
    max-width: 100%;
    padding: 0px;
}

.leaflet-container {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.navbar .form-inline {
  min-width: 500px;
}

.navbar a {
  color: darkgray;
}
.navbar a::after {
  content: none;
}

.navbar .form-inline input {
  min-width: 500px;
}

.navbar img {
  margin-top: -8px;
  height: 40px;
}

.tagsContainer{
  box-shadow: none;
  background-color: transparent;
  border: none;
}

.tagsContainer::before{
  box-shadow: none;
  background-color: transparent;
  border: none;
}

.main-brand {
  padding-top: 0px;
  font-size: 1.75em;
  font-family: 'Lobster', cursive;
}

#settings.nav-item  {
  color: darkgray;
  padding-left: 10px;
  padding-top: 4px;
}

img.leaflet-div-icon {
  width: 16px;
  height: 16px;
  border-radius: 8px;
  background: darkgray;
  opacity: 0.8;
  border: 0px;
}

body > div.ReactModalPortal > div.overlay-after-open {
  z-index: 400;
}

div.multiselect-container {
  width: 400px;
}